// ===================== src/hooks/useLocalStorage.js =====================
import { useEffect, useState } from "react";

/**
 * Robust localStorage hook
 * - Safe JSON parse/stringify
 * - Supports functional updaters (like useState)
 * - Works during SSR (no window)
 * - Syncs across tabs via `storage` event
 * - Removes key when value === undefined
 */
export default function useLocalStorage(key, initialValue) {
  const isBrowser =
    typeof window !== "undefined" && typeof window.localStorage !== "undefined";

  const init = () =>
    typeof initialValue === "function" ? initialValue() : initialValue;

  const readValue = () => {
    if (!isBrowser) return init();
    try {
      const item = window.localStorage.getItem(key);
      return item !== null ? JSON.parse(item) : init();
    } catch {
      return init();
    }
  };

  const [value, setValue] = useState(readValue);

  // Write to localStorage whenever value changes (same-tab)
  const setStoredValue = (updater) => {
    setValue((prev) => {
      const next = typeof updater === "function" ? updater(prev) : updater;
      if (isBrowser) {
        try {
          if (next === undefined) {
            window.localStorage.removeItem(key);
          } else {
            window.localStorage.setItem(key, JSON.stringify(next));
          }
          // Notify same-tab listeners
          window.dispatchEvent(new StorageEvent("storage", { key }));
        } catch {}
      }
      return next;
    });
  };

  // Re-read when key changes
  useEffect(() => {
    setValue(readValue());
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [key]);

  // Sync across tabs/windows
  useEffect(() => {
    if (!isBrowser) return;
    const onStorage = (e) => {
      if (e.key === key) setValue(readValue());
    };
    window.addEventListener("storage", onStorage);
    return () => window.removeEventListener("storage", onStorage);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [key]);

  return [value, setStoredValue];
}
