// src/App.js
import React, { Suspense, lazy } from "react";
import { BrowserRouter, Routes, Route } from "react-router-dom";
import Layout from "./components/common/Layout";
import { CartProvider } from "./context/CartContext";
import { AuthProvider } from "./context/AuthContext";
import ScrollToTop from "./components/common/ScrollHelpers";

//  keep CSS imports at top-level (before any code runs)
import "./index.css";
import "./components/auth/login.css";

//  lazy pages
const Home = lazy(() => import("./pages/Home"));
const Products = lazy(() => import("./pages/Products"));
// If your detail page lives in components/, change the path accordingly:
// const ProductDetail = lazy(() => import("./components/product/ProductDetail"));
const ProductDetail = lazy(() => import("./pages/ProductDetail"));
const CartPage = lazy(() => import("./pages/Cart"));
const Checkout = lazy(() => import("./pages/Checkout"));
const Wishlist = lazy(() => import("./pages/Wishlist"));

const LoadingSpinner = () => (
  <div className="min-h-screen flex items-center justify-center">
    <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-orange-500" />
  </div>
);

export default function App() {
  return (
    <AuthProvider>
      <BrowserRouter>
        <CartProvider>
          <ScrollToTop />
          <Suspense fallback={<LoadingSpinner />}>
            <Routes>
              {/* Layout with Outlet inside */}
              <Route element={<Layout />}>
                <Route index element={<Home />} />
                <Route path="products" element={<Products />} />
                {/*  matches links like /product/60 */}
                <Route path="product/:id" element={<ProductDetail />} />
                <Route path="wishlist" element={<Wishlist />} />
                <Route path="cart" element={<CartPage />} />
                <Route path="checkout" element={<Checkout />} />
              </Route>
            </Routes>
          </Suspense>
        </CartProvider>
      </BrowserRouter>
    </AuthProvider>
  );
}
