// src/context/AuthContext.js
import React, { createContext, useContext, useEffect, useState } from "react";

const AuthCtx = createContext(null);
export const useAuth = () => useContext(AuthCtx);

export function AuthProvider({ children }) {
  const [user, setUser] = useState(() => {
    try { return JSON.parse(localStorage.getItem("auth:user")) || null; }
    catch { return null; }
  });
  const [pendingAction, setPendingAction] = useState(null); // queued fn to run after login

  useEffect(() => {
    if (user) localStorage.setItem("auth:user", JSON.stringify(user));
    else localStorage.removeItem("auth:user");
  }, [user]);

  const login = ({ email, password, name }) => {
    // fake validation
    const u = { name: name || (email ? email.split("@")[0] : "Guest"), email: email || "guest@local" };
    setUser(u);
    if (pendingAction) {
      // run queued action (e.g., addToCart)
      const fn = pendingAction;
      setPendingAction(null);
      fn();
    }
  };

  const register = ({ name, email, password, phone }) => {
    setUser({ name, email, phone: phone || "" });
    if (pendingAction) {
      const fn = pendingAction;
      setPendingAction(null);
      fn();
    }
  };

  const logout = () => setUser(null);

  return (
    <AuthCtx.Provider value={{
      user,
      isLoggedIn: !!user,
      login,
      register,
      logout,
      pendingAction,
      setPendingAction
    }}>
      {children}
    </AuthCtx.Provider>
  );
}
