// src/components/common/ScrollHelpers.jsx
import React, { useEffect, useState } from "react";
import { useLocation } from "react-router-dom";
import "./scroll-top-button.css";

export default function ScrollHelpers({ behavior = "smooth" }) {
  const { pathname, search, hash, key } = useLocation();
  const [showBtn, setShowBtn] = useState(false);

  // Auto scroll on route change
  useEffect(() => {
    const roots = [
      document.scrollingElement || document.documentElement,
      ...Array.from(document.querySelectorAll("[data-scroll-root], .app-scroll")),
    ];
    roots.forEach((el) => {
      try { el.scrollTo({ top: 0, left: 0, behavior }); }
      catch { el.scrollTop = 0; el.scrollLeft = 0; }
    });
  }, [pathname, search, hash, key, behavior]);

  // Show button after some scroll
  useEffect(() => {
    const onScroll = () => setShowBtn(window.scrollY > 250);
    onScroll();
    window.addEventListener("scroll", onScroll, { passive: true });
    return () => window.removeEventListener("scroll", onScroll);
  }, []);

  const handleClick = () =>
    window.scrollTo({ top: 0, left: 0, behavior: "smooth" });

  return (
    <button
      type="button"
      className={`scrolltop-btn ${showBtn ? "is-visible" : ""}`}
      onClick={handleClick}
      aria-label="Scroll to top"
    >
      <svg width="22" height="22" viewBox="0 0 24 24" aria-hidden="true">
        <path d="M21,9H15V22H13V16H11V22H9V9H3V7H21M12,2A2,2 0 0,1 14,4A2,2 0 0,1 12,6C10.89,6 10,5.1 10,4C10,2.89 10.89,2 12,2Z" />
      </svg>
    </button>
  );
}
