// ===================== src/context/CartContext.js =====================
import React, { createContext, useContext, useMemo } from "react";
import useLocalStorage from "../hooks/useLocalStorage";
import { useAuth } from "./AuthContext";
import { useNavigate, useLocation } from "react-router-dom";

const CartContext = createContext(undefined);

export const CartProvider = ({ children }) => {
  // Persisted states
  const [cartItems, setCartItems] = useLocalStorage("cart", []);
  const [wishlist, setWishlist] = useLocalStorage("wishlist", []); //  array of product IDs
  const [orders, setOrders] = useLocalStorage("orders", []);

  // Auth + navigation (for login guard)
  const { isLoggedIn, setPendingAction } = useAuth?.() || {};
  const navigate = useNavigate();
  const location = useLocation();

  // ---------------- CART ----------------
  const realAddToCart = (item, qty = 1) => {
    setCartItems((prev) => {
      const found = prev.find((p) => p.id === item.id);
      return found
        ? prev.map((p) =>
            p.id === item.id ? { ...p, quantity: (p.quantity || 0) + qty } : p
          )
        : [...prev, { ...item, quantity: qty }];
    });
  };

  const addToCart = (item, qty = 1) => {
    if (!isLoggedIn || isLoggedIn === true) return realAddToCart(item, qty);
    setPendingAction?.(() => () => realAddToCart(item, qty));
    const next = encodeURIComponent(location.pathname + location.search);
    navigate(`/checkout?auth=login&next=${next}`);
  };

  const updateQty = (id, qty) =>
    setCartItems((prev) =>
      qty <= 0
        ? prev.filter((p) => p.id !== id)
        : prev.map((p) => (p.id === id ? { ...p, quantity: qty } : p))
    );

  const updateQuantity = updateQty; // alias
  const remove = (id) => updateQty(id, 0);
  const clearCart = () => setCartItems([]);

  // ---------------- WISHLIST ----------------
  //  the exact helpers you asked to add:
  const isWished = (id) => wishlist?.includes(id);
  const toggleWishlist = (id) =>
    setWishlist((prev) =>
      prev.includes(id) ? prev.filter((x) => x !== id) : [...prev, id]
    );
  const removeFromWishlist = (id) =>
    setWishlist((prev) => prev.filter((x) => x !== id));

  // ---------------- TOTALS ----------------
  const totals = useMemo(() => {
    const totalItems = cartItems.reduce((n, i) => n + (i.quantity || 0), 0);
    const totalPrice = cartItems.reduce(
      (n, i) => n + (i.price || 0) * (i.quantity || 0),
      0
    );
    return { totalItems, totalPrice };
  }, [cartItems]);

  // ---------------- ORDERS ----------------
  const placeOrder = ({ address, paymentMethod }) => {
    const order = {
      id: Date.now().toString(),
      items: [...cartItems],
      total: totals.totalPrice,
      address,
      paymentMethod,
      estimatedDelivery: "25-30 minutes",
      timestamp: new Date().toISOString(),
    };
    setOrders((prev) => [...prev, order]);
    clearCart();
    return order;
  };

  // ---------------- CONTEXT VALUE ----------------
  const value = {
    // cart
    items: cartItems,
    cartItems, // legacy alias
    addToCart,
    add: addToCart, // legacy alias
    updateQty,
    updateQuantity,
    remove,
    clearCart,

    // wishlist
    wishlist,
    isWished,
    toggleWishlist,
    toggleWish: toggleWishlist, // legacy alias
    removeFromWishlist,

    // orders
    orders,
    placeOrder,

    // totals
    totals,
    totalItems: totals.totalItems,
    getTotalItems: () => totals.totalItems,
    getTotalPrice: () => totals.totalPrice,
  };

  return <CartContext.Provider value={value}>{children}</CartContext.Provider>;
};

export const useCart = () => {
  const ctx = useContext(CartContext);
  if (!ctx) throw new Error("useCart must be used within a CartProvider");
  return ctx;
};

export default CartContext;
