import React, { useState } from "react";
import { Link, useLocation, useNavigate } from "react-router-dom";
import {
  ShoppingCart,
  User,
  Heart,
  Search,
  MapPin as MapPinIcon,
  Utensils,
  Package,
} from "lucide-react";
import { useCart } from "../../context/CartContext";
import { useAuth } from "../../context/AuthContext";
import "./header.css";

export default function Header() {
  const { totalItems, wishlist } = useCart();
  const { user, logout } = useAuth();

  const [q, setQ] = useState("");
  const nav = useNavigate();                     // use nav everywhere
  const loc = useLocation();
  const isActive = (p) => loc.pathname === p;

  return (
    <nav className="nav">
      <div className="nav__container">
        <div className="nav__row">
          {/* -------- Left: Logo + Primary Nav -------- */}
          <div className="nav__left">
            <Link to="/" className="logo">
              <span className="logo__emoji"></span> WadaPav
              <span className="logo__pill">Mumbai Special</span>
            </Link>

            <div className="nav__links">
              <Link to="/" className={`nav-link ${isActive("/") ? "is-active" : ""}`}>
                <MapPinIcon size={16} className="nav-link__icon" /> Home
              </Link>

              <Link
                to="/products"
                className={`nav-link ${isActive("/products") ? "is-active" : ""}`}
              >
                <Utensils size={16} className="nav-link__icon" /> Menu
              </Link>

              <Link
                to="/cart"
                className={`nav-link ${isActive("/cart") ? "is-active" : ""}`}
              >
                <Package size={16} className="nav-link__icon" /> Orders
              </Link>
            </div>
          </div>

          {/* -------- Right: Search + Wishlist + Cart + User -------- */}
          <div className="nav__right">
            {/* Search */}
            <div className="search">
              <input
                value={q}
                onChange={(e) => setQ(e.target.value)}
                onKeyDown={(e) => {
                  if (e.key === "Enter") nav(`/products?q=${encodeURIComponent(q)}`);
                }}
                placeholder="Search food..."
                className="search__input"
              />
              <Search className="search__icon" size={20} />
            </div>

            {/* Wishlist */}
            <button
              className={`icon-btn wish-toggle ${wishlist?.length ? "has-items" : ""}`}
              aria-label={`Wishlist ${wishlist?.length ? `(${wishlist.length})` : ""}`}
              onClick={() => nav("/wishlist")}
            >
              <Heart
                size={20}
                className={wishlist?.length ? "fill-current text-red-500" : "text-slate-600"}
              />
              {wishlist?.length > 0 && (
                <span className="badge badge--pink">{wishlist.length}</span>
              )}
            </button>

            {/* Cart */}
            <Link to="/cart" className="icon-btn icon-btn--cart" aria-label="Cart">
              <ShoppingCart size={20} />
              {totalItems > 0 && <span className="badge badge--red">{totalItems}</span>}
            </Link>

            {/* User dropdown / login links */}
            {user ? (
              <div className="user group">
                <button className="user__btn">
                  <User size={18} />
                  <span className="user__name">{user.name}</span>
                </button>
                <div className="user__menu">
                  <Link to="/cart" className="user__item">My Orders</Link>
                  <hr className="user__sep" />
                  <button onClick={logout} className="user__item user__item--danger">
                    Logout
                  </button>
                </div>
              </div>
            ) : (
              <div className="auth">
                <Link to="/checkout?auth=login" className="btn btn--light">Login</Link>
                <Link to="/checkout?auth=register" className="btn btn--outline">Register</Link>
              </div>
            )}
          </div>
        </div>
      </div>
    </nav>
  );
}
